module.exports = "@import url(\"https://fonts.googleapis.com/css?family=Montserrat\");\n/* ==========================================================================\n   Variables\n   ========================================================================== */\n/*Taken from W3C */\n/* The Overlay (background) */\n.overlay {\n  /* Height & width depends on how you want to reveal the overlay (see JS below) */\n  height: 100%;\n  width: 0;\n  position: fixed;\n  /* Stay in place */\n  z-index: 100 !important;\n  /* Sit on top */\n  left: 0;\n  top: 0;\n  background-color: black;\n  /* Black fallback color */\n  background-color: rgba(0, 0, 0, 0.9);\n  /* Black w/opacity */\n  overflow-x: hidden;\n  /* Disable horizontal scroll */\n  transition: 0.5s;\n  /* 0.5 second transition effect to slide in or slide down the overlay (height or width, depending on reveal) */ }\n/* Position the content inside the overlay */\n.overlay-content {\n  position: relative;\n  top: 25%;\n  /* 25% from the top */\n  width: 100%;\n  /* 100% width */\n  text-align: center;\n  /* Centered text/links */\n  margin-top: 30px;\n  /* 30px top margin to avoid conflict with the close button on smaller screens */ }\n/* The navigation links inside the overlay */\n.overlay a {\n  padding: 8px;\n  text-decoration: none;\n  font-size: 36px;\n  color: #818181;\n  display: block;\n  /* Display block instead of inline */\n  transition: 0.3s;\n  /* Transition effects on hover (color) */ }\n/* When you mouse over the navigation links, change their color */\n.overlay a:hover, .overlay a:focus {\n  color: #f1f1f1; }\n/* Position the close button (top right corner) */\n.overlay .closebtn {\n  position: absolute;\n  top: 20px;\n  right: 45px;\n  font-size: 60px; }\n/* When the height of the screen is less than 450 pixels, change the font-size of the links and position the close button again, so they don't overlap */\n@media screen and (max-height: 450px) {\n  .overlay a {\n    font-size: 20px; }\n  .overlay .closebtn {\n    font-size: 40px;\n    top: 15px;\n    right: 35px; } }\n.hamburger {\n  font-size: 3rem;\n  padding: 3rem; }\n@media (min-width: 992px) {\n  #sideNav {\n    text-align: center;\n    position: fixed;\n    top: 0;\n    left: 0;\n    display: flex;\n    flex-direction: column;\n    width: 19rem;\n    height: 100vh;\n    background-color: white;\n    padding-right: 2rem !important;\n    padding-left: 2rem !important;\n    border-right: .05rem solid #dcdcdc !important; }\n    #sideNav .navbar-brand {\n      display: flex;\n      margin: auto auto 0;\n      padding: 0.5rem; }\n      #sideNav .navbar-brand .img-profile {\n        max-width: 10rem;\n        max-height: 10rem;\n        border: 0.5rem solid rgba(255, 255, 255, 0.2); }\n    #sideNav .navbar-collapse {\n      display: flex;\n      align-items: flex-start;\n      flex-grow: 0;\n      width: 100%;\n      margin-bottom: auto; }\n      #sideNav .navbar-collapse .navbar-nav {\n        flex-direction: column;\n        width: 100%; }\n        #sideNav .navbar-collapse .navbar-nav .nav-item {\n          display: block; }\n          #sideNav .navbar-collapse .navbar-nav .nav-item .hr-text {\n            line-height: 1em;\n            position: relative;\n            outline: 0;\n            border: 0;\n            color: black;\n            text-align: center;\n            height: 1.5em;\n            opacity: .5; }\n            #sideNav .navbar-collapse .navbar-nav .nav-item .hr-text:before {\n              content: '';\n              background: linear-gradient(to right, transparent, #818078, transparent);\n              position: absolute;\n              left: 0;\n              top: 50%;\n              width: 100%;\n              height: 1px; }\n            #sideNav .navbar-collapse .navbar-nav .nav-item .hr-text:after {\n              content: attr(data-content);\n              position: relative;\n              display: inline-block;\n              color: black;\n              padding: 0 .5em;\n              line-height: 1.5em;\n              color: #818078;\n              background-color: #fcfcfa; }\n          #sideNav .navbar-collapse .navbar-nav .nav-item .nav-link {\n            display: block;\n            color: black;\n            font-size: 1.5rem; }\n          #sideNav .navbar-collapse .navbar-nav .nav-item a:hover {\n            color: red; } }\n"